<template>
  <div class="ServiceAdminEdit">
    <h2>基本信息</h2>
    <el-divider></el-divider>
    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">昵称：</div>
        <el-input v-model="form.name"></el-input>
      </el-col>
      <el-col :span="8">
        <div class="name" style="width:106px;">性别：</div>
        <el-select placeholder="活动区域" style="width:245px;">
          <el-option label="男" value="1"></el-option>
          <el-option label="女" value="0"></el-option>
        </el-select>
      </el-col>
    </el-row>
    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">家乡城市：</div>
        <el-input v-model="form.hometownCity"></el-input>
      </el-col>
      <el-col :span="8">
        <div class="name">学校：</div>
        <el-input v-model="form.schoolName"></el-input>
      </el-col>
    </el-row>
    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">出生日期：</div>
        <el-date-picker
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          style="width:660px"
        ></el-date-picker>
      </el-col>
      <el-col :span="8">
        <div class="name">个性签名：</div>
        <el-input v-model="form.autograph"></el-input>
      </el-col>
    </el-row>
    <h2>图片资料</h2>
    <el-divider></el-divider>
    <el-row :gutter="50">
      <el-col :span="8">
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          accept=".png, .jpg.jif"
          list-type="picture-card"
          :file-list="imgFileList"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
          :on-progress="handleImgSuccess"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>
      </el-col>
    </el-row>
    <h2>时间出售信息</h2>
    <el-divider></el-divider>
    <el-row :gutter="50">
      <el-col :span="300">
        <div class="name">技能介绍：</div>
        <el-input type="textarea" style="width:600px;"></el-input>
      </el-col>
    </el-row>

    <el-row :gutter="50">
      <el-col :span="18">
        <div class="name">视频介绍：</div>
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          accept=".mp4"
          list-type="picture-card"
          :file-list="videoFileList"
          :on-preview="handleVideoCardPreview"
          :on-remove="handleRemove"
          :on-progress="handleVideoSuccess"
        >
          <i class="el-icon-plus"></i>
        </el-upload>

        <el-dialog title :visible.sync="dialogPlay" @close="closeVideoDialog">
          <video :src="dialogVideoUrl" controls autoplay width="100%"></video>
        </el-dialog>
      </el-col>
    </el-row>

    <el-row :gutter="50">
      <el-col :span="24">
        <div class="name">每分钟收费：</div>
        <div>
          <el-radio
            v-for="item in 3"
            :key="item"
            v-model="radio2"
            label="1"
            border
            size="medium"
          >50优币</el-radio>
          <el-input placeholder="自定义" size="medium" style="width:100px; margin-left: 30px;"></el-input>
        </div>
      </el-col>
    </el-row>

    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">收入提成：</div>
        <el-input></el-input>
      </el-col>
      <el-col :span="8">
        <div class="name">礼物提成：</div>
        <el-input></el-input>
      </el-col>
    </el-row>

    <el-row :gutter="50">
      <el-col :span="8">
        <el-button @click="close">关闭</el-button>
        <el-button @click="preservation">保存</el-button>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imgFileList: [
        {
          name: "1.jpg",
          url:
            "http://tupian.qqjay.com/tou2/2018/1106/60bdf5b88754650e51ccee32bb6ac8ae.jpg"
        }
      ],
      videoFileList: [
        {
          name: "1.mp4",
          url: "http://vfx.mtime.cn/Video/2019/02/04/mp4/190204084208765161.mp4"
        }
      ],
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false,
      dialogVideoUrl: "",
      dialogPlay: false,
      form: {
        name: "王大拿", //昵称
        sex: 1, //性别
        hometownCity: "", //家乡城市
        schoolName: "", //学校
        birthday: "", //出生日期
        autograph: "" //个性签名
      }
    };
  },
  methods: {
    //图片
    handleRemove(file) {
      console.log(file);
      console.log(this.imgFileList);
    },
    //上传图片
    handleImgSuccess(response, file, fileList) {
      console.log(response);
      this.imgFileList.push({
        name: file.name,
        url: file.url
      });
      console.log(fileList);
    },
    handleVideoSuccess(response, file, fileList) {
      console.log(response);
      this.videoFileList.push({
        name: file.name,
        url: file.url
      });
      console.log(fileList);
    },
    //查看图片
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleDownload(file) {
      console.log(file);
    },
    //查看视频
    handleVideoCardPreview(file) {
      this.dialogVideoUrl = file.url;
      this.dialogPlay = true;
    },
    //关闭视频时的回调
    closeVideoDialog() {
      this.dialogVideoUrl = "";
    },
    // 保存
    preservation() {
      this.$confirm("是否保存？", {
        confirmButtonText: "保存",
        cancelButtonText: "取消"
      })
        .then(() => {
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$router.go(-1);
        })
        .catch(() => {});
    },
    //关闭
    close() {
      this.$confirm("是否关闭？", {
        confirmButtonText: "关闭",
        cancelButtonText: "取消"
      }).then(() => {
        this.$router.go(-1);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.ServiceAdminEdit {
  padding-bottom: 8px;
}
h2 {
  padding-top: 30px;
}
</style>